import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Observable} from 'rxjs';
import { Subscription } from 'rxjs';

import {UsersService} from './../../shared/services/users.service';
import { Users, Posts } from './../../shared/models/user.model';




@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit,OnDestroy {
  private subscriptions = new Subscription();
  public posts : Posts[];
  public userid: number;
  private skip: number = 0;
  private limit: number = 10;
  public username : string;
  constructor(private _userservice: UsersService, private _router: Router, private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.userid = +this._activatedRoute.snapshot.paramMap.get('id');
    this.username =  localStorage.getItem('username');
    this.getUserposts();
  }

  getUserposts(){
    this.subscriptions.add(this._userservice.getUserPost(this.userid, this.skip, this.limit).subscribe((data)=> { 
      this.posts = data;
    }));
  }

  previous(){
    if(this.skip > 10){
     this.skip = this.skip - 10;
    }
    this.getUserposts();
    }
  next() {
    this.skip = this.skip + 10;
    this.getUserposts();
  }
  
  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }
   
 
}
