import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import {catchError} from 'rxjs/operators';
import { Users, Posts, Comments } from './../models/user.model';
import { query } from '@angular/core/src/render3/query';


@Injectable({
  providedIn: 'root'
})
export class UsersService {
  
  private API_URL = 'https://jsonplaceholder.typicode.com/';
  constructor(private _httpClient: HttpClient) { }

  getUserList():Observable<Users[]> {
  return this._httpClient.get<Users[]>(`${this.API_URL}users`);
  }
  
  getUserPost(userid, skip?: number, limit?: number): Observable<Posts[]> {
      const params = { userid: userid, skip: skip, limit: limit };
       let queryParams = '';
     if(skip && limit) { queryParams = `&skip=${skip}&limit=${limit}`}
     return this._httpClient.get<Posts[]>(`${this.API_URL}posts?userId=${userid}${queryParams}` );
  }

  getPostDetails(postid): Observable<Posts> {
   return this._httpClient.get<Posts>(`${this.API_URL}posts/${postid}` );
  }

  getPostComments(postid): Observable<Comments[]> {
    return this._httpClient.get<Comments[]>(`${this.API_URL}comments?postId=${postid}`);
  }
  deletePosts(postid) {
    return this._httpClient.get(`${this.API_URL}posts/${postid}`).pipe(
      catchError(this.handleError)
    )
  }

  handleError(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    window.alert(errorMessage);
    return throwError(errorMessage);
  }
  
}