import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Observable} from 'rxjs';
import { Subscription } from 'rxjs';

import {UsersService} from './../../shared/services/users.service';
import { Users, Posts, Comments } from './../../shared/models/user.model';

@Component({
  selector: 'app-post-details',
  templateUrl: './post-details.component.html',
  styleUrls: ['./post-details.component.css']
})
export class PostDetailsComponent implements OnInit, OnDestroy {
  private subscriptions = new Subscription();
  public post: Posts;
  public postid: number;
  public comments: Comments[];
  public showComments: boolean = false;
  public username : string = '';
  
  constructor(private _userservice: UsersService, private _router: Router, private _activatedRoute: ActivatedRoute) { }


  ngOnInit() {
    this.postid = +this._activatedRoute.snapshot.paramMap.get('postid');
     this.getPostDetails();
     this.username =  localStorage.getItem('username');
  }

  getPostDetails(){
    this.subscriptions.add(this._userservice.getPostDetails(this.postid).subscribe((data)=> { 
      this.post = data;
       }));
     this.subscriptions.add(this._userservice.getPostComments(this.postid).subscribe((data)=> { 
       this.comments = data;
     }));
  }
  loadComments() {
    this.showComments = !this.showComments;
    }

  deletePosts() {
    this._userservice.deletePosts(this.postid);
    this._router.navigate(['/user-posts' , this.post.userId]);
  }
  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }
}
